.product-info-main {
	.product-info-price {
		border-bottom: initial !important;
	}
}
.page-product-bundle
{
	.popup-recommended-product
	{
		display: none;
	}
	.product-gallery
	{
		&.desktop
		{
			margin-top: 0rem;
		}
	}
}
.product.media {
	display: block;
	.product-gallery {
		.items {
			.item {
				position: relative;
				text-align: right;
				display: inline-block;
				.download-button {
					position: absolute;
					bottom: 0;
					right: 0;
					.low-resolution {
						font-size: @font-size--milli;
						background-color: @color-white !important;
						height: 4rem;
						line-height: 4rem;
						letter-spacing: 0.1em;
						text-transform: uppercase;
						color: @color-dark-charcoal;
						padding: 1rem 2rem 1.5rem 3.5rem;
						text-decoration: none;
						background: url(../images/download-icon.png) no-repeat;
						background-position: 1.5rem 0.8rem;
					}
					.high-resolution {
						font-size: @font-size--milli;
						background-color: @color-white !important;
						height: 4rem;
						line-height: 4rem;
						letter-spacing: 0.1em;
						text-transform: uppercase;
						color: @color-dark-charcoal;
						padding: 1rem 1.5rem 1.5rem 3.5rem;
						text-decoration: none;
						background: url(../images/download-icon.png) no-repeat;
						background-position: 1.5rem 0.8rem;
					}
				}
			}
		}
	}
	.product-next-prev {
    	margin: 3rem 0 1.4rem 0;
		float: right;
		width: 142px;
		height: 52px;
		.product-next {
			font-size: @font-size--base;
			line-height: 19px;
			text-align: center;
			color: @color-white;
			background: @color-ivory-black;
			padding: 1.5rem 5.5rem;
			text-decoration: none;
			float: right;
			font-family: @font-family--primary;
		}
		.product-prev {
            display: none !important;
		}

	}
}
.additional-attributes-wrapper {
	.additional-attributes {
		>tbody {
			>tr {
				>th {
					border: none;
					padding: 5.5px 30px 10px 0;
					width: 151px;
					height: 17px;
					left: 0%;
					right: 64.72%;
					top: 9.6%;
					bottom: 89.05%;
					font-family: @font-family--primary;
					font-style: normal;
					font-weight: 900;
					font-size: @font-size--milli;
					line-height: 17px;
					letter-spacing: 0.15em;
					text-transform: uppercase;
				}
				>td {
					border: none;
					padding: 5.5px 5px 10px;
					top: 9.6%;
					font-family: @font-family--primary;
					font-style: normal;
					font-weight: normal;
					font-size: @font-size--milli;
					line-height: 1.7rem;
					width: 14.6rem;
					height: 1.7rem;
				}
			}
		}
	}
}
.table {
	>tbody {
		>tr {
			>th {
				border-top: 1px solid #ccc !important;
			}
			>td {
				border-top: 1px solid #ccc !important;
			}
		}
	}
}


.catalog-product-view{
	.breadcrumbs
	{
		margin-bottom: 20px;
	}
	.product.info.detailed
	{
		display: none;
	}
	.column.main{
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		max-width: 1190px !important;
		margin: auto;
		.product-info-main{
			width: 35%;
			margin-top: 5px;
			order: 2;

			@media (max-width: (@screen__m - 1)){
				max-width: 100%;
			}

			.product-info-price{
				display: flex;
				flex-wrap: wrap;

				.product-info-stock-sku{
					order: 0;
					padding: 0px;
					width: 100%;
					text-align: left;

					.stock {
						font-size: 12px;
						line-height: 14px;
						font-weight: 900;
						font-family: @font-family--secondary;

						&.available{
							color:@color-midnight-green;
						}
					}

					.sku{
						font-size: 14px;
						line-height: 17px;
						color: @color-ivory-black;
					}
				}
			}

			.additional-attributes-wrapper{
				.additional-attributes {

					width: 100%;
					table-layout: fixed;

					@media (max-width: 639px){
						display: table;
					}

					& > tbody {
						@media (max-width: 639px){
							display: table-row-group;
						}

						& > tr {
							@media (max-width: 639px){
								display: table-row;
							}

							& > th {
								border-top: 0.5px solid rgba(35, 31, 32, 20%) !important;
								padding: 10px 5px 10px 0px;

								@media (max-width: 639px){
									display: table-cell;
									vertical-align: middle;
									background: none;
								}
							}

							td.col.data.hbft_price_list {
								a.hbft_price_list {
									font-weight: normal;
									font-size: 14px;
									line-height: 20px;
									letter-spacing: 0.1em;
									text-transform: uppercase;
									color: #333333 !important;
								}
							}

							& > td {
								border-top: 0.5px solid rgba(35, 31, 32, 20%) !important;
								padding: 10px 5px 10px 0px;

								@media (max-width: 639px){
									display: table-cell;
									padding: 10px 5px 10px 0px !important;

									&::before{
										display: none;

									}
								}

								&.lead_time {
									color: #231f20;
								}
								&.hbft_price_list{
									a{
										padding: 0px 0px 0px 23px;
										position: relative;

										&:before{
											content: "";
											width: 13px;
											height: 13px;
											background: url(../images/pricelists_icon.png) no-repeat;
											display: inline-block;
											position: absolute;
											left: 0px;
											top: 2px;

										}
									}
								}


								a{
									color: @color-dark-charcoal;
									text-decoration: underline;
								}

								.download_attribute_list{
									float: left;
									width: 100%;
									margin: 0px 0px 20px;

									a{
										float: left;
										color: @color-dark-charcoal;
										width: 100%;



										svg{
											float: left;
											margin-right: 10px;
											margin-top: 2px;
										}

										&::before{
											margin-right: 10px;
										}


										&.download_maintenance{
