// start liked product slider css
.catalog-product-view {
	.block.related {
		width: 100%;
		max-width: 1160px;
		margin: auto;
		.block-title.title{
			margin-bottom: 40px;
			strong#block-related-heading {
				font-size: 16px;
				font-family: 'NeuzeitS-bold','sans-serif';
				text-transform: uppercase;
				color: #231F20;
			}
		}
	}
}

.custom-slider{
	.products {
		margin: 0;
	}
}

.liked-product-slider {
    width: 75%;
	li.item.product-item {
		padding-right: 15px;
		.product-item-info{
			width: 100%;
			&:hover,
			&:focus {
				box-shadow: none;
				border: 0;
				margin: 0;
				padding: 0;
			}
			.product-item-photo {
				max-width: 200px;
				width: 200px;
				height: 200px;
				clear: both;
				display: block;
				margin: 0 0 10px 0 !important;
				.product-image-container {
					width: 100% !important;
					.product-image-wrapper {
						width: 200px;
						height: 200px;
						padding-bottom: 0 !important;
						.product-image-photo {
							margin: 0;
							width: 100%;
							height: 200px;
							max-width: 200px;
							max-height: 200px;
							object-fit: cover;
						}
					}
				}
			}
			.product-item-details {
				.product-item-name {
					margin: 0;
					font-family: 'NeuzeitS-bold','sans-serif';
					font-size: 12px;
					color: #231F20;
					a:hover{
						text-decoration: none;
					}
				}
				.price-box.price-final_price {
					margin: 0;
					.price-container{
						span.price-label {
							display: none;
						}
						.price-wrapper {
							.price, .measurement {
								font-family: 'NeuzeitS-bold','sans-serif';
								font-size: 12px;
								color: #231F20;
								text-transform: lowercase;
							}
						}
					}
					.old-price, .supplies_text {
						display: none;
					}
				}
				.product-item-actions {
					a.action.tocompare {
						display: none;
					}
					.towishlist {
						display: none;
						position: absolute;
						top: -10px;
						right: 0;
					}
				}
			}
			.form_text {
				display: none;
			}
		}
	}
	.slick-arrow {
		width: 25px;
    	height: 50px;
		background: transparent;
    	margin-top: -22px;
		&:before {
			content: "";
			border: solid #231F20;
			border-width: 0 1px 1px 0;
			display: inline-block;
			padding: 8px;
		}
	}
	.slick-arrow.slick-disabled {
		opacity: 0;
	}
	.slick-arrow.slick-next {
		right: -20px;
		&::before {
			transform: rotate(-45deg);
			-webkit-transform: rotate(-45deg);
		}
		&:hover, &:focus{
			color: #231F20;
			outline: none;
			background: transparent;
		}
	}
	.slick-arrow.slick-prev {
		left: -30px;
		&::before {
			transform: rotate(135deg);
			-webkit-transform: rotate(135deg);
		}
		&:hover, &:focus{
			color: #231F20;
			outline: none;
			background: transparent;
		}
	}
}

.catalog-category-view {
	.form_text {
		display: none;
	}
}

@media (max-width: (@screen__m - 1)) {
	.block.related {
		width: 100%;
		max-width: 100%;
	}
	.liked-product-slider {
		width: 100%;
		.slick-arrow {
			background: #E5E5E5;
		}
		.slick-arrow.slick-next {
			right: 0px;
			padding-right: 10px;
			&:hover, &:focus{
				background: #E5E5E5;
			}
		}
		.slick-arrow.slick-prev {
			left: 0px;
			padding-left: 9px;
			&:hover, &:focus{
				background: #E5E5E5;
			}
		}
		.slick-slide.slick-current.slick-active {
			width: 215px !important;
		}
	}
}

