							}

							.item-options{
								margin: 1px 0px 0px 0px;

								@media (max-width: 600px){
									margin-left: auto;
									margin-right: auto;
								}


								dt{
									font-size: @font-size--base;
									line-height: 19px;
									color: @color-ivory-black;
									float: left;
									font-family: @font-family--secondary;
									margin: 0px 6px 0px 0px;
								}
								dd{
									font-size: @font-size--base;
									line-height: 19px;
									color: @color-ivory-black;
									margin: 0px;
									display: block;

									@media (max-width: 600px){
										display: block;
										text-align: left;

									}


									&.tags{
                                        display: -webkit-box;
                                        vertical-align: top;
                                        overflow: hidden;
                                        -webkit-line-clamp: 1;
                                        -webkit-box-orient: vertical;
										text-overflow: ellipsis;
										word-break: break-all;
									}

									.price{
										display: inline-block;
										margin: 0px 0px 0px 6px;
										line-height: normal;
										vertical-align: top;
									}
								}
							}
							.item-price{

								margin: 23px 0px 0px;

								.price{
									font-size: @font-size--kilo;
									line-height: 22px;
									color: @color-ivory-black;
									font-family: @font-family--secondary;
								}
							}
						}
					}

					&.cart-detail{
						padding: 0px 40px 0px 0px;
						text-align: right;
						width:275px;

						@media (max-width: (@screen__m - 1)){
							width: 100%;
							padding: 0px 15px;
							margin: 15px 0px 0px;

						}

						& > span{
							font-size: 22px;
							line-height: 26px;
							color: @color-ivory-black;
							margin: 0px 0px 18px;
							display: block;
							padding: 0px 19px 0px 0px;

							@media (max-width: 600px){
								padding: 0px;
								text-align: center;
							}

						}

						button{
							width: 206px !important;
							height: 52px;
							background: @color-ivory-black;
							border: none;
							max-width: 100%;
							color: @color-white;
							border-radius: 0px;
							margin: 0px 0px 7px;
							font-size: 1.6rem;
							line-height: 19px;
							font-weight: normal;
							vertical-align: top;
							padding: 10px;
							font-family: @font-family--primary;

							@media (max-width: 600px){
								width: 100% !important;
								margin: 0px;
							}

							&:nth-of-type(2){
								background: @color-white;
								border: 1px solid @color-ivory-black;
								color: @color-ivory-black;
								margin: 0px;

								@media (max-width: 600px){
									margin: 7px 0px 0px;
								}
							}
						}
					}
				}

				.note{
					font-size: @font-size--kilo;
					line-height: 21px;
					color: @color-ivory-black;
					padding: 0px 43px 0px 35px;
					display: inline-block;
					vertical-align: top;
					margin: 12px 0px 0px;
					width: 100%;
				}
			}
		}
	}
}

